


















































































// https://github.com/zhetengbiji/mpvue-picker
import mpvuePicker from '../../components/mpvue-picker/mpvuePicker.vue';
import timeData from '../../common/time.data.js';
import QQMapWX from '../../common/qqmap-wx-jssdk.min.js'
import uniSegmentedControl from '../../components/uni-segmented-control/uni-segmented-control.vue';
import uniIcon from "../../components/uni-icon/uni-icon.vue"
import uniTag from "../../components/uni-tag/uni-tag.vue"

var qqmapsdk = new QQMapWX({
	key: 'S6PBZ-76735-PTZIT-Q27QH-LKQQO-FQBTW'
})

export default {
	components: {
		mpvuePicker,
		uniSegmentedControl,
		uniIcon,
		uniTag
	},
	data() {
		return {
			current: 0,
			items: [
				'乘客',
				'司机'
			],
			styleType: 'button',
			activeColor: '#2c2c2c',

			passenger: {
				origin: {
					location: "",
					longitude: 0,
					latitude: 0,
				},
				destination: {
					location: "",
					longitude: 0,
					latitude: 0,
				},
				nums: 1,
				planTime: "",
				distance: '',
				estimatedPrice: '',
				
				showMessage: false
			},

			numsArray: [{
				label: 1,
				value: 1
			}, {
				label: 2,
				value: 2
			}, {
				label: 3,
				value: 3
			}, {
				label: 4,
				value: 4
			}],
			isSingle: true,
			pickerValueArray: [],
			pickerValueDefault: [],
			themeColor: '#2c2c2c',
			mode: '',
			deepLength: 3
		}
	},
	methods: {
		chooseLocation(obj) {
			wx.showLoading({
			  title: '加载中',
			})
			uni.chooseLocation({
				success(res) {
					obj.location = res.name
					obj.latitude = res.latitude
					obj.longitude = res.longitude
				},
				complete() {
					wx.hideLoading()
				}
			});
		},
		bindPickerChange: function(e) {
			console.log(e);
			this.passengerNumIndex = e.detail.value;
		},
		bindDateChange: function(e) {
			this.dateValue = e.detail.value;
		},
		formSubmit: function(e) {
			wx.showToast({
				title: '请观察控制台',
				icon: 'loading'
			});
			console.log(JSON.stringify(e.detail.value));
		},
		showSinglePicker() {
			this.isSingle = true
			this.mode = 'selector'
			this.pickerValueArray = this.numsArray
			this.pickerValueDefault = [0]
			this.$refs.mpvuePicker.show()
		},
		showMulPicker() {
			this.isSingle = false
			this.pickerValueArray = timeData
			this.mode = 'multiSelector'
			var date = new Date()
			var hour = date.getHours()
			var minute = date.getMinutes()
			this.pickerValueDefault = [0, hour, minute]
			this.$refs.mpvuePicker.show()
		},
		onPickerConfirm(e, obj) {
			console.log(e)
			if (this.isSingle) {
				obj.nums = e.label
			} else {
				var date = new Date()
				var hour = date.getHours()
				var minute = date.getMinutes()
				if (e.index[0] === 0 && (e.index[1] < hour || e.index[1] === hour && e.index[2] < minute)) {
					wx.showToast({
						title: '不能小于当前时间',
						icon: 'none'
					});
				} else {
					obj.planTime = e.label
				}
			}
		},
		getDistance(obj) {
			if (obj.origin.latitude !== 0 && obj.destination.latitude !== 0) {
				console.log(obj)
				obj.showMessage = true;
				obj.distance = '计算中...'
				obj.estimatedPrice = '计算中...'
				qqmapsdk.direction({
					mode: 'driving',
					from: {
						latitude: obj.origin.latitude,
						longitude: obj.origin.longitude
					},
					to: {
						latitude: obj.destination.latitude,
						longitude: obj.destination.longitude
					},
					success: function(res) {
						console.log(res)
						obj.distance = (res.result.routes[0].distance / 1000).toFixed(2).toString() + 'km'
						obj.estimatedPrice = (res.result.routes[0].distance / 1000 * 0.85).toFixed(2) + '元'
					},
					fail: function(error) {
						console.error(error);
					},
					complete: function(res) {
						console.log("qqmap complete");
					}
				})
			}
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
	},
	computed: {
		passengerDistance() {
			return this.getDistance(this.passenger)
		}
	}
}
